<template>
  <v-container grid-list-xl>
    <v-overlay :value="is_loading_products || loading" opacity=".92">
      <v-col class="d-flex flex-column justify-center align-center">
        <v-progress-circular indeterminate color="blue" size="64" width="5"></v-progress-circular>
        <div class="white--text overline text-center mt-5">cargando</div>
      </v-col>
    </v-overlay>
    <v-row no-gutters>
      <v-row cols="6" md="6" align="center" justify="center">
        <product-card v-for="product in productArray[0]" :key="product._id" :producto="product" />
      </v-row>
      <v-row cols="6" md="6" align="center" justify="center">
        <product-card v-for="product in productArray[1]" :key="product._id" :producto="product" />
      </v-row>
      <!-- <v-row cols="12" md="12" align="center">
        <product-card v-for="product in productArray[2]" :key="product._id" :producto="product" />
      </v-row> -->
    </v-row>
  </v-container>
</template>

<script>
import AppBar from "@/components/appbar";
import ProductCard from "@/components/productCard";

import { mapActions, mapState } from "vuex";

Object.defineProperty(Array.prototype, "chunk", {
  value: function(chunkSize) {
    var R = [];
    for (var i = 0; i < this.length; i += chunkSize)
      R.push(this.slice(i, i + chunkSize));
    return R;
  }
});

export default {
  data: () => ({
    loading: false
  }),
  components: {
    ProductCard
  },
  computed: {
    ...mapState("products", ["products", "categoria", "is_loading_products"]),
    productArray() {
      this.loading = true
      if (this.products.length > 0) {
        let arr = []
        if (this.categoria == "Todos") {
          arr = this.products
          arr = arr.chunk(arr.length / 2);
          setTimeout(()=>this.loading = false, 1500)
        }else{
          arr = this.products.filter(e => {
            if (e.categoria == this.categoria) return e;
          });
          arr = arr.chunk(arr.length / 2);
        setTimeout(()=>this.loading = false, 1500)
        }
        return arr;
      } else return [];
    },
    
  },
  async created() {
    await this.$store.dispatch("products/getProducts");
    await this.$store.commit("products/setFavoritos");
  },
  async beforeCreate(){
    await this.$store.dispatch(
      "favoritos/getFavoritos",
      this.$store.state.auth.usuario.token
    );
  
  },
};
</script>