<template>
  <v-list>
    <v-list-item two-line>
      <v-list-item-avatar color="blue darken-3">
        <span class="white--text caption">{{getUserAvatar}}</span>
      </v-list-item-avatar>

      <v-list-item-content>
        <v-list-item-title>{{nombre}}</v-list-item-title>
        <v-list-item-subtitle>{{email}}</v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>

    <v-divider></v-divider>
    <v-list-item v-for="item in items" :key="item.title" link @click="$router.push(item.to)">
      <v-list-item-icon>
        <v-icon>{{ item.icon }}</v-icon>
      </v-list-item-icon>

      <v-list-item-content>
        <v-list-item-title>{{ item.title }}</v-list-item-title>
      </v-list-item-content>
    </v-list-item>
  </v-list>
</template>

<script>
export default {
  props: ["nombre", "email"],
  data: () => ({
    items: [
      { title: "Inicio", icon: "fa-store", to: "/" },
      { title: "Perfil", icon: "fa-user", to: "/perfil" },
      { title: "Favoritos", icon: "fa-heart", to: "/favoritos" },
      { title: "Compras", icon: "fa-boxes", to: "/favoritos" }
    ],
  }),
  computed: {
    getUserAvatar() {
      let name = this.nombre.split(" ");
      return name[0][0] + (name[1] ? name[1][0] : "");
    },
  },
};
</script>