<template>
  <v-app>
    <app-bar />
    
    <v-main
      id="scrolling-techniques-5"
      class="overflow-y-auto"
      style="height: 100vh"
      :style="getPadding"
    >
      <router-view />
    <!-- <dialog-compra/> -->
    </v-main>
  </v-app>
</template>

<script>
import AppBar from "@/components/appbar";
import DialogCompra from '@/components/terminarCompra'
import { mapState } from "vuex";

export default {
  data: () => ({
    collapseOnScroll: true,
  }),
  components: {
    AppBar,
    DialogCompra
  },
  computed: {
    getPadding() {
      if (this.is_logged) {
        return "padding-top: 200px";
      } else {
        return "padding-top: 100px";
      }
    },
    ...mapState("auth", ["is_logged"]),
  },
};
</script>