import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', name: 'Home', component: Home },
  { path: '/favoritos', name: 'favoritos', component: ()=> import('../views/Favoritos.vue') },
  { path: '/login', name: 'Login', component: ()=> import('../views/Login.vue') },
  { path: '/perfil', name: 'Perfil', component: ()=> import('../views/Perfil.vue') },
  { path: '/carrito', name: 'Carrito', component: ()=> import('../views/Carrito.vue') },
  {
    path: '/product/:id',
    name: 'Product',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Product.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  const publicPages = ['/login', '/home'];
  const authRequired = !publicPages.includes(to.path);
  const loggedIn = JSON.parse(localStorage.getItem('sesion'));
  // console.log(loggedIn)

  // trying to access a restricted page + not logged in
  // redirect to login page
  if (authRequired && !loggedIn) {
    next('/login');
  } else {
    if(loggedIn && to.path == "/login"){
      next('/')
    }else
    next();
  }
});

export default router
