import Usuario from './models/usuario'
import router from '../router'

const API_URL = 'https://radiant-oasis-39313.herokuapp.com/api'
const API_URL_LOCAL = 'http://localhost:5000/api'

export const auth = {
    namespaced: true,
    state: {
        usuario: localStorage.getItem('sesion')
            ? JSON.parse(localStorage.getItem('sesion'))
            : null,
        status: {
            show: false,
            type: 'info',
            msg: 'test'
        },
        is_logged: localStorage.getItem('logged')
            ? JSON.parse(localStorage.getItem('logged'))
            : null,
    },
    mutations: {
        setUsuario(state, data) {
            state.status.show = true
            state.status.type = 'success'
            state.status.msg = 'Iniciando Sesion'
            setTimeout(() => {
                state.status.show = false
                state.is_logged = true
                router.push('/')
                localStorage.setItem('logged', JSON.stringify(state.is_logged))
            }, 2500)

            state.usuario = new Usuario(data)
            localStorage.setItem('sesion', JSON.stringify(state.usuario))
        },
        logout(state) {
            state.usuario = null,
                state.is_logged = false
            localStorage.removeItem('sesion')
            localStorage.removeItem('logged')
            localStorage.removeItem('favoritos')
        },
        createAlert(state, data) {
            state.status.show = true
            if (data.status === "AUTH_REJECTED")
                state.status.type = 'warning'
            if (data.status === "NOT_FOUND")
                state.status.type = 'error'
            if (data.status === "SUCCESS")
                state.status.type = 'success'
            state.status.msg = data.error
            setTimeout(() => {
                state.status.show = false
            }, 3500)
        },
        async refrescarInformacion(state, data) {
            const req = await fetch(API_URL + '/users/find', {
                method: 'POST',
                body: JSON.stringify({ email: data }),
                headers: {
                    'Content-type': 'application/json'
                }
            })
            const res = await req.json()
            if (res.status === "OK") {
                state.status.show = true
                state.status.type = 'success'
                state.status.msg = 'Iniciando Sesion'
                setTimeout(() => {
                    state.status.show = false
                    state.is_logged = true
                    router.push('/')
                    localStorage.setItem('logged', JSON.stringify(state.is_logged))
                }, 2500)

                state.usuario = new Usuario(res.usr_data)
                localStorage.setItem('sesion', JSON.stringify(state.usuario))
                localStorage.setItem('favoritos', JSON.stringify(state.usuario.favoritos))
            }
        }
    },
    actions: {
        async login({ commit }, data) {
            const req = await fetch(API_URL + '/auth/login', {
                method: 'POST',
                body: JSON.stringify(data),
                headers: {
                    'Content-type': 'application/json'
                }
            })
            const res = await req.json()
            if (res.status !== "OK")
                commit('createAlert', res)

            else
                commit('setUsuario', res.usr_data)
        },
        async registrar({ commit }, data) {
            const req = await fetch(API_URL + '/users/add', {
                method: 'POST',
                body: JSON.stringify(data),
                headers: {
                    'Content-type': 'application/json'
                }
            })
            const res = await req.json()
            if (res.status !== "OK")
                commit('createAlert', res)

            else {
                commit('createAlert', {
                    status: 'SUCCESS',
                    error: "Usuario registrado",
                })
            }
        },
        async actualizarInformacion({ commit }, data) {
            console.log(data)
            const req = await fetch(API_URL + '/users/edit', {
                method: 'PUT',
                body: JSON.stringify(data),
                headers: {
                    'Content-type': 'application/json'
                }
            })
            const res = await req.json()
            if (res.status === "OK") {
                commit("refrescarInformacion", data.userToChange)
            }
        },
        async actualizarPass({ commit }, data){
            const req = await fetch(API_URL + '/auth/changePassword', {
                method: 'POST',
                body: JSON.stringify(data),
                headers: {
                    'Content-type': 'application/json'
                }
            })
            const res = await req.json()
        }
    },
}