<template>
  <div>
    <v-app-bar
      absolute
      color="#1E88E5"
      dark
      dense
      shrink-on-scroll
      scroll-target="#scrolling-techniques-5"
      style="z-index: 100"
      :src="getIMG"
      app>
      <template v-slot:img="{ props }">
      <v-img v-bind="props" gradient="to top right, #263238f5, #212121f5 "></v-img>
      </template>

      <v-app-bar-nav-icon v-if="is_logged" @click.stop="drawer = !drawer" :icon="'fa-mobile'" />

      <v-toolbar-title class="text-uppercase align-self-center" @click="goback">
          <v-img :src="getIMG" height="50px" width="150"/>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-badge
        v-if="is_logged"
        :content="in_cart"
        :value="in_cart"
        color="red"
        class="mt-3 mr-4"
        overlap
      >
        <v-icon @click="$router.push('/carrito')">fa-shopping-cart</v-icon>
      </v-badge>
      <!-- <v-icon v-if="is_logged" class="mt-5 mr-4" @click="$router.push('/favoritos')">fa-heart</v-icon> -->
      <template v-slot:extension v-if="is_logged">
        <v-select
          :items="['Todos', 'Bocinas', 'Micrófonos', 'All In One', 'PC de escritorio', 'Pantallas y TV', 'Pantallas de Proyección', 'Cámara Digital', 'Motherboards']"
          v-model="_categoria"
          @input="updateCategoria"
          label="Categorias"
          solo-inverted
          hide-details
          class="mx-1 mt-1 pb-2"
          dense
          flat
          outlined
          v-if="root"
          max-width="350"
        />
        <search-component/>
      </template>
    </v-app-bar>
    <v-navigation-drawer
      v-if="is_logged"
      class="blue darken-2"
      dark
      overflow
      app
      width="350"
      v-model="drawer"
      style="z-index:100">
      <drawer-items :nombre="usuario.nombre" :email="usuario.email"/>

      <template v-slot:append>
          <v-btn block @click="cerrarSesion" class="rounded-0" color="blue darken-4">Cerrar Sesion</v-btn>
      </template>
    </v-navigation-drawer>
  </div>
</template>

<script>
import { mapState } from "vuex";
import SearchComponent from '@/components/search'
import DrawerItems from '@/components/drawer'

export default {
  data: () => ({
    messages: 1,
    show: false,
    _categoria: "",
    drawer: null
  }),
  computed: {
    ...mapState(["info", "root"]),
    ...mapState("products", ["categoria"]),
    ...mapState("auth", ["is_logged", "usuario"]),
    ...mapState("shopping_cart", ["in_cart"]),
    getIMG(){
      return require('@/assets/hohopcs2.png')
    }
  },
  methods: {
    updateCategoria() {
      this.$store.commit("products/updateCategoria", this._categoria);
      // console.log(this._categoria, this.categoria)
    },
    goback() {
      this.$router.push("/");
    },
    cerrarSesion() {
      this.$store.commit("auth/logout");
      this.$router.push("/login");
    }
  },
  created() {
    this.$store.commit("navigate", false);
    this._categoria = this.categoria;
  },
  components: {
    SearchComponent,
    DrawerItems
  }
};
</script>