import Vue from 'vue'
import Vuex from 'vuex'
// Modulos
import {products} from './products'
import {auth} from './auth'
import {shopping_cart} from './shoppingCart'
import {favoritos} from './favoritos'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    info: { msg: null },
    root: true,
    mensajes_error: [
      {msg: '¡El dogo hace Guau!', icon: 'fa-dog'},
      {msg: 'Se necesitaron 5 tarros de cerveza para lograr este sitio web, no eran totalmente necesarios pero del programador se rehusaba a trabajar sin su bebida', icon: 'fa-beer'},
      {msg: '¿Sandias que la sandia es roja?', icon: 'fa-kiwi-bird'},
      {msg: '¿Ha oido hablar de la palabra de las PWA?', icon: 'fa-heart'},
    ],
    msg_e: {msg: '¡El dogo hace Guau!', icon: 'fa-dog'},
    drawer: null,
  },
  mutations: {
    initialiseStore(state) {
      if (localStorage.getItem('data'))
        state = JSON.parse(localStorage.getItem('data'))
    },
    saveState(state) {
      localStorage.setItem('data', JSON.stringify(state))
    },
    setInfo(state, n) {
      console.log('m: ' + n)
      state.info.msg = n
    },
    navigate(state, root){
      state.root = root
    },
    getMSGERROR(state){
      let num = Math.floor(Math.random() * state.mensajes_error.length)
      state.msg_e = state.mensajes_error[num]
    },
    openMenu(state){
      state.drawer = !state.drawer
    }
  },
  actions: {
    changeInfo({ commit }, i) {
      console.log(i)
      commit('setInfo', i)
    }
  },
  modules: {
    products,
    auth,
    shopping_cart,
    favoritos
  }
})
