import CRequestHandler, {API_URL, API_URL_LOCAL} from './models/gestorPeticiones'

export const shopping_cart = {
    namespaced: true,
    state: {
        cart: [],
        total: 0.0,
        in_cart: 0,
        decodedList: {
            status: 'LOADING',
            list: []
        }
    },
    mutations: {
        async add(state, item){
            let productToPush = state.cart.find(e => e.producto === item.producto)
            if(productToPush === undefined){
                productToPush = item
                state.cart.push(productToPush)
            }else{
                productToPush.cantidad += item.cantidad
            }
            state.total += item.precio
            state.in_cart+= item.cantidad

            const response = await CRequestHandler.post(API_URL_LOCAL + '/products/addToCart', {
                userID: JSON.parse(localStorage.getItem('sesion')).token,
                carritoNuevo: {
                    cart: state.cart,
                    total: state.total,
                    in_cart: state.in_cart
                } 
            })
        },
        async decodeCarrito(state){
            const list = state.cart.map(e =>{
                return e.producto
            })
            const req = await fetch(API_URL + '/products/findList',{
                method: 'POST',
                body: JSON.stringify(list),
                headers: {
                    'Content-Type': 'application/json'
                }
            })
            const res = await req.json()
            if(res.status === "OK"){
                state.decodedList = {
                    status: 'LOADED',
                    list: res.data
                }
            }
        },
        async obtenerCarrito(state){
            const response = await CRequestHandler.post(API_URL_LOCAL + '/users/obtenerCarrito', {
                userID: JSON.parse(localStorage.getItem('sesion')).token
            })
            if(response.status === "OK"){
                console.log('obtenerCarrito: ', response)
                state.cart = response.data.cart
                state.total = response.data.total
                state.in_cart = response.data.in_cart
            }
            
        },
    },
    actions: {
        agregarAlCarrito({ commit }, item){
            if(item.cantidad > 0){
                item.precio = item.precio * item.cantidad
                commit('add', item)
            }
        },
        async eliminarCarrito({ commit }){
            const response = await CRequestHandler.post(API_URL_LOCAL + '/products/addToCart', {
                userID: JSON.parse(localStorage.getItem('sesion')).token,
                carritoNuevo: {
                    cart: [],
                    total: 0,
                    in_cart: 0
                } 
            })
            if(response.status === "OK"){
                commit('obtenerCarrito')
            }
        }
    },
}