<template>
    <v-autocomplete
        v-model="model"
        :items="items"
        :loading="isLoading"
        :search-input.sync="search"
        class="mx-1 mt-1 pb-2"
        hide-selected
        item-text="Description"
        item-value="API"
        label="Public APIs"
        placeholder="Start typing to Search"
        prepend-inner-icon="fa-search"
        return-object
        hide-details
        solo-inverted
        clearable
        flat
        outlined
        dense
        @change="openItem"
      ></v-autocomplete>
</template>

<script>
  export default {
    data: () => ({
      descriptionLimit: 60,
      entries: [],
      isLoading: false,
      model: null,
      search: null,
    }),

    computed: {
      fields () {
        if (!this.model) return []

        return Object.keys(this.model).map(key => {
          return {
            key,
            value: this.model[key] || 'n/a',
          }
        })
      },
      items () {
        return this.entries.map(entry => {
          const Description = entry.descripcion.length > this.descriptionLimit
            ? entry.name + ' ' + entry.descripcion.slice(0, this.descriptionLimit) + '...'
            : entry.name + ' ' + entry.descripcion

          return Object.assign({}, entry, { Description })
        })
      },
    },

    watch: {
      search (val) {
        // Items have already been loaded
        if (this.items.length > 0) return

        // Items have already been requested
        if (this.isLoading) return

        this.isLoading = true

        // Lazily load input items
        fetch('https://radiant-oasis-39313.herokuapp.com/api/products/list')
          .then(res => res.json())
          .then(res => {
            this.count = res.length
            this.entries = res
          })
          .catch(err => {
            console.log(err)
          })
          .finally(() => (this.isLoading = false))
      },
    },
    methods: {
      openItem(){
        if(this.model){
          this.$router.push(`/product/${this.model._id}`)
          this.$store.commit("products/showProduct", this.model._id);
        }
      }
    }
  }
</script>