import CRequestHandler, { API_URL, API_URL_LOCAL } from './models/gestorPeticiones'
import Product from './models/product'

export const favoritos = {
    namespaced: true,
    state: {
        listaFavoritos: [],
        contadorFavoritos: 0,
        listaFavoritosCompleta: []
    },
    mutations: {
        async refreshFavoritos(state, id) {
            const response = await CRequestHandler.post(API_URL + '/favoritos/list', { userID: id })
            if(response.status === "OK"){
                state.listaFavoritos = response.data
                state.contadorFavoritos = response.data.length
                localStorage.setItem('favoritos', JSON.stringify(state.listaFavoritos))
            }
        },
        async getListaFavoritosCompleta(state){
            const response = await CRequestHandler.post(API_URL + '/products/findList', state.listaFavoritos)
            if(response.status === "OK"){
                state.listaFavoritosCompleta = response.data.map(e => {
                    let _product = new Product(e)
                    _product.selected = true
                    return _product
                })
                state.contadorFavoritos = response.data.length
            }
        },
        async deleteItem(state, product) {
            let index = state.listaFavoritosCompleta.indexOf(product)
            let i = state.listaFavoritos.indexOf(product._id)
            if(index !== -1 || i !== -1){
                state.listaFavoritos.splice(i, 1)
                state.listaFavoritosCompleta.splice(index, 1)
                state.contadorFavoritos--
            }
            localStorage.setItem('favoritos', JSON.stringify(state.listaFavoritos))
        }
    },
    actions: {
        getFavoritos({ commit }, id) {
            commit('refreshFavoritos', id)
        },
        async addFavorito({ commit }, info) {
            const response = await CRequestHandler.post(API_URL + '/favoritos/add', {
                userID: info.id,
                productID: info.product
            })
            if(response.status === "OK")
            commit('refreshFavoritos', info.id)
        },
        async eliminarFavorito({commit}, info){
            const response = await CRequestHandler.post(API_URL + '/favoritos/remove', {
                userID: info.id,
                productID: info.product._id
            })
            if(response.status === "OK"){
                await commit('deleteItem', info.product)
            }
        }
    }
}