<template>
  <v-btn
    absolute
    dark
    fab
    right
    top
    text
    class="mt-4"
    :color="getColor"
    @click.stop="favorito"
    style="right: 0 !important; top: 15 !important"
  >
    <v-icon>fa-heart</v-icon>
  </v-btn>
</template>

<script>
export default {
  props: ["selected", "productID"],
  computed: {
    getColor() {
      return this.selected ? "red darken-1" : "blue-grey lighten-1";
    },
    request() {
      return {
        id: this.$store.state.auth.usuario.token,
        product: this.productID,
      };
    },
  },
  methods: {
    favorito() {
      if (this.selected) {
        this.$store.dispatch("favoritos/eliminarFavorito", {
          id: this.$store.state.auth.usuario.token,
          product: {_id: this.productID},
        });
      } else {
        this.$store.dispatch("favoritos/addFavorito", {
          id: this.$store.state.auth.usuario.token,
          product: this.productID,
        });
      }
      this.$emit("invertSelected");
    },
  },
};
</script>