import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import store from './store'
import vuetify from './plugins/vuetify';
import 'roboto-fontface/css/roboto/roboto-fontface.css'
import '@fortawesome/fontawesome-free/css/all.css'

import { Plugins, AppState} from '@capacitor/core'
import router from './router'
import { mapActions } from 'vuex';
const { Network, Modals, SplashScreen } = Plugins
const CA = Plugins.App

Vue.config.productionTip = false

new Vue({
  store,
  vuetify,
  beforeCreate(){
    CA.addListener('backButton', async () => {
      // let alertRet = await Modals.alert({
      //   title: 'Router',
      //   message: router.history.current.name
      // });
      if(['Home', 'Login'].indexOf(router.history.current.name) === -1){
        router.go(-1)
      }else{
        // this.$store.commit('saveState')
        CA.exitApp()
      }
    })
    // this.$store.commit('initialiseStore')
    window.onbeforeunload = ()=>{
      // this.$store.commit('saveState')
      // return 'sodf'
    }
  },
  router,
  mounted(){
    this.$store.commit('shopping_cart/obtenerCarrito')
  },
  render: h => h(App)
}).$mount('#app')
