import Product from './models/product'

export const products = {
    namespaced: true,
    state: {
        products: [],
        categoria: 'Todos',
        is_loading_products: true,
        producto: null,
    },
    mutations: {
        setProducts(state, products) {
            state.products = products.map(e => new Product(e))
            setTimeout(() => { state.is_loading_products = false }, 3500)
        },
        updateCategoria(state, categoria) {
            state.categoria = categoria
        },
        showProduct(state, id) {
            state.producto = state.products.find(e => e._id === id)
            // if(state.favoritos.indexOf(id) !== -1){
            //     state.producto.selected = true
            // }
        },
        setFavoritos(state) {
            let favoritos = JSON.parse(localStorage.getItem('favoritos'))
            favoritos.forEach(e => {
                let f = state.products.find(p => p._id === e)
                f.selected = true
            })
        },
    },
    actions: {
        async getProducts({ commit }) {
            const req = await fetch('https://radiant-oasis-39313.herokuapp.com/api/products/list');
            const res = await req.json()
            console.log(res)
            commit('setProducts', res)
        }
    }
}