export default new class {
    
    async post(url, data) {
        const req = await fetch(url, {
            method: 'POST',
            body: JSON.stringify(data),
            headers: {
                'Content-Type': 'application/json'
            }
        })
        
        return await req.json()
    }
    
    async get(url) {
        const req = await fetch(url)
        return await req.json()
    }
    
}
export const API_URL = 'https://radiant-oasis-39313.herokuapp.com/api'
