<template>
  <v-lazy
    v-model="isActive"
    :options="{ threshold: .5 }"
    min-height="200"
    transition="fade-transition"
  >
    <v-card
      class="mx-2 mb-4 align-self-center"
      outlined
      width="42vw"
      max-width="350"
      @click.prevent="goTo"
      :ripple="false"
    >
      <v-img :src="getIMG" style="opacity: 0.8">
        <boton-favorito
          :selected="producto.selected"
          :productID="producto._id"
          @invertSelected="producto.selected = !producto.selected"
        />
      </v-img>
      <v-row no-gutters>
        <v-col cols="12">
          <v-card-title class="pa-4 pb-0 text-left body-2">{{producto.name}}, {{producto.descripcion}}</v-card-title>
        </v-col>
        <v-col cols="12">
          <v-card-subtitle
            class="pa-4 pb-2 text-left"
          >{{producto.departamento}} | {{producto.categoria}}</v-card-subtitle>
        </v-col>
      </v-row>

      <v-card-text class="px-4 pt-0">
        <v-row no-gutters class="text-left mx-0">
          <v-col>
            <v-card-title class="pa-2 pb-0 px-0 text-left body-1 font-weight-bold">{{formatMoney(producto.precio)}}</v-card-title>
          </v-col>
          <v-col cols="12">
            <v-rating
              :value="getRate"
              background-color="blue darken-4"
              dense
              half-increments
              readonly
              size="14"
            ></v-rating>
          </v-col>
          <v-col cols="12">
            <div class="grey--text">{{producto.precio}} | Existencia: {{producto.existencia }}</div>
          </v-col>
        </v-row>
        <!-- 
      <div class="my-4 subtitle-1">{{producto.categoria}}</div>

        <div>{{producto.descripcion}}</div>-->
      </v-card-text>
    </v-card>
  </v-lazy>
</template>

<script>
import BotonFavorito from "@/components/favbtn";
export default {
  data: () => ({
    isActive: false,
  }),
  props: ["producto"],
  computed: {
    getIMG() {
      return this.producto.img
        ? require(`@/assets/products/${this.producto.img}`)
        : require(`@/assets/logo.png`);
    },
    getRate() {
      return this.producto.name == "PATATA MADRE" ? 5 : Math.random() * 5;
    },
  },
  methods: {
    goTo() {
      this.$router.push("product/" + this.producto._id);
    },
    formatMoney(mount) {
      return mount.toLocaleString("en-US", {
        style: "currency",
        currency: "MXN",
      });
    },
  },
  components: {
    BotonFavorito,
  },
};
</script>